: var(--main-color);\n  font-size: 1.5rem;\n  font-weight: 500;\n  margin-bottom: 10px;\n}\n\ninput[type=\"radio\"] {\n  accent-color: var(--main-color);\n}\n\n.confirm-button {\n  align-self: flex-end;\n  margin-right: 10px;\n  color: white;\n  background-color: var(--highlight-color);\n  padding: 5px 8px;\n  border: none;\n  border-radius: 8px;\n  font-size: 1.1rem;\n  font-weight: 600;\n  cursor: pointer;\n  box-shadow:\n    inset 0 -1px 2px rgba(0, 0, 0, 0.5),\n    inset 0 1px 2px rgba(255, 255, 255, 0.2);\n}\n\n.confirm-button:hover {\n  background-color: white;\n  color: var(--base-color);\n}\n\n.confirm-button:disabled {\n  background-color: #ccc;\n  color: #666;\n  cursor: not-allowed;\n}\n\n.confirm-button:focus,\ninput:focus {\n  outline: none;\n}\n\n#game-setting {\n  width: 350px;\n}\n\n#game-setting p {\n  font-size: 1.15rem;\n}\n\n.form-body {\n  margin: 3px 0 15px 0;\n  display: flex;\n  justify-content: space-between;\n  font-size: 1.05rem;\n}\n\n.form-body > div:first-child {\n  margin-right: 30px;\n}\n\n.form-body input,\n.form-body label {\n  cursor: pointer;\n}\n\n#game-setup {\n  padding: 15px 50px;\n}\n\n#game-setup p {\n  font-size: 1.8rem;\n}\n\n#setup {\n  display: grid;\n  grid-template-columns: auto auto;\n  align-items: center;\n  gap: 15px 30px;\n  margin: 20px 0;\n}\n\n#dock {\n  display: grid;\n  grid-template-columns: repeat(3, max-content);\n  justify-items: center;\n  align-items: center;\n  gap: 20px;\n}\n\n#dock > div:first-child {\n  grid-column: 1 / 4;\n  font-size: 1.5rem;\n}\n\n.docked-ship {\n  display: flex;\n}\n\n.ship-element {\n  border: 4px solid var(--main-color);\n  width: 40px;\n  height: 40px;\n  border-radius: 4px;\n}\n\n.horizontal,\n.vertical {\n  width: 40px;\n  filter: invert(100%) brightness(1000%);\n  border: 1.5px solid black;\n  cursor: pointer;\n}\n\n.hidden {\n  visibility: hidden;\n  pointer-events: none;\n}\n\n.move {\n  position: absolute;\n  transition: transform 0.02s linear;\n  align-self: flex-start;\n  pointer-events: none;\n}\n\n.vertical-ship {\n  flex-direction: column;\n}\n\n#setup-grid {\n  display: grid;\n  grid-template-columns: repeat(10, max-content);\n  grid-template-rows: repeat(10, max-content);\n}\n\n.grid-element {\n  border: 1px solid white;\n  width: 40px;\n  height: 40px;\n  transition: background-color 0.2s ease-out;\n}\n\n#setup-reset,\n#randomize {\n  justify-self: center;\n  cursor: pointer;\n}\n\n#transition,\n#repeat {\n  min-width: 910px;\n  min-height: 280px;\n}\n\n#transition h1,\n#repeat h1 {\n  font-size: 2.5rem;\n}\n\n#transition p,\n#repeat p {\n  font-size: 2rem;\n}\n\n#main-container {\n  width: 75%;\n  display: flex;\n  justify-content: space-between;\n  flex-wrap: wrap;\n  row-gap: 50px;\n}\n\n#main-container.no-display {\n  display: none;\n}\n\n#container-one,\n#container-two {\n  display: grid;\n  gap: 30px;\n  justify-items: center;\n}\n\n#grid-one,\n#grid-two {\n  display: grid;\n  justify-items: center;\n  grid-template-columns: repeat(10, max-content);\n  grid-template-rows: repeat(10, max-content);\n  padding: 20px;\n  background-color: var(--bg-lighter-color);\n  border: none;\n  border-radius: 15px;\n  box-shadow:\n    inset 0 -4px 6px rgba(0, 0, 0, 0.5),\n    inset 0 4px 6px rgba(255, 255, 255, 0.2);\n  cursor: pointer;\n}\n\n.unclickable {\n  position: relative;\n  pointer-events: none;\n}\n\n.unclickable::before {\n  content: \"\";\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  pointer-events: auto;\n  cursor: not-allowed;\n}\n\n.status-container {\n  display: grid;\n  justify-items: center;\n  gap: 10px;\n  padding: 5px 20px 20px 20px;\n  background-color: var(--bg-lighter-color);\n  border: none;\n  border-radius: 15px;\n  box-shadow:\n    inset 0 -4px 6px rgba(0, 0, 0, 0.5),\n    inset 0 4px 6px rgba(255, 255, 255, 0.2);\n}\n\n.status-container > div:first-child {\n  font-size: 1.3rem;\n  font-weight: 500;\n}\n\n#status-one,\n#status-two {\n  display: grid;\n  grid-auto-flow: column;\n  gap: 15px;\n}\n\n.ship-status {\n  display: grid;\n  grid-auto-flow: column;\n}\n\n.status-element {\n  width: 20px;\n  height: 40px;\n  border: 2px solid var(--main-color);\n  border-radius: 2px;\n  transition: background-color 0.3s ease-out;\n}\n\n.green {\n  background-color: rgba(38, 115, 76, 0.5);\n}\n\n.red {\n  background-color: rgba(255, 0, 0, 0.5);\n}\n\n.yellow {\n  background-color: rgba(255, 255, 0, 0.5);\n}\n\nfooter {\n  padding: 3px 0;\n  margin-top: 20px;\n  font-size: 0.9rem;\n}\n\n@media (max-width: 1200px) {\n  body {\n    background-image: url(./assets/dark-ocean-tablet.jpg);\n  }\n\n  #main-container {\n    justify-content: center;\n  }\n\n  #transition,\n  #repeat {\n    min-width: 600px;\n    min-height: 250px;\n  }\n\n  #setup {\n    row-gap: 20px;\n    justify-items: center;\n  }\n\n  #dock,\n  #setup-grid {\n    grid-column: 1 / 3;\n  }\n}\n\n@media (max-width: 600px) {\n  body {\n    background-image: url(./assets/dark-ocean-phone.jpg);\n  }\n\n  header > img {\n    width: 60px;\n  }\n\n  header > p {\n    font-size: 2.5rem;\n  }\n\n  dialog h1 {\n    font-size: 1rem;\n    margin-bottom: 5px;\n  }\n\n  .confirm-button {\n    font-size: 1rem;\n  }\n\n  #game-setting {\n    width: 280px;\n  }\n\n  #game-setup {\n    padding: 15px 30px;\n  }\n\n  #game-setup p {\n    font-size: 1.3rem;\n  }\n\n  #setup {\n    gap: 10px;\n    margin: 10px 0;\n  }\n\n  #dock {\n    gap: 10px;\n  }\n\n  #dock > div:first-child {\n    font-size: 1rem;\n  }\n\n  .ship-element {\n    border-width: 3px;\n    width: 30px;\n    height: 30px;\n    border-radius: 3px;\n  }\n\n  .horizontal,\n  .vertical {\n    width: 30px;\n    border-width: 1.5px;\n  }\n\n  .grid-element {\n    border-width: 0.75px;\n    width: 30px;\n    height: 30px;\n  }\n\n  #setup-reset,\n  #randomize {\n    font-size: 0.8rem;\n  }\n\n  #transition,\n  #repeat {\n    min-width: 300px;\n    min-height: 150px;\n  }\n\n  #transition h1,\n  #repeat h1 {\n    font-size: 1.3rem;\n  }\n\n  #transition p,\n  #repeat p {\n    font-size: 1rem;\n  }\n\n  #main-container {\n    row-gap: 25px;\n  }\n\n  #container-one,\n  #container-two {\n    gap: 15px;\n  }\n\n  #grid-one,\n  #grid-two {\n    padding: 10px;\n    border-radius: 8px;\n  }\n\n  .status-container {\n    gap: 5px;\n    padding: 3px 10px 10px 10px;\n    border-radius: 8px;\n  }\n\n  .status-container > div:first-child {\n    font-size: 1rem;\n  }\n\n  #status-one,\n  #status-two {\n    gap: 12px;\n  }\n\n  .status-element {\n    width: 15px;\n    height: 30px;\n    border-width: 1.5px;\n    border-radius: 1.5px;\n  }\n\n  footer {\n    padding: 3px 0;\n    margin-top: 10px;\n    font-size: 0.7rem;\n  }\n}\n"],"sourceRoot":""}]);
